.container {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background: #fff;
    overflow: hidden;
}

.container::before {
    content: '';
    position: absolute;
    width: 2000px;
    height: 2000px;
    border-radius: 50%;
    background: linear-gradient(-45deg , #4481eb , #04befe);
    top: -10%;
    right: 48%;
    transform: translateY(-50%);
    z-index: 6;
    transition: 1.8s ease-in-out;
}

.forms__container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.signin__signup {
    position: absolute;
    top: 50%;
    left: 75%;
    transform: translate(-50% , -50%);
    width: 50%;
    display: grid;
    grid-template-columns: 1fr;
    z-index: 5;
    transition: 1s .7s ease-in-out;
}

.signin__signup > form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 5rem;
    overflow: hidden;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    transition: .2s .7s ease-in-out;
}

form.signin__form {
    z-index: 2;
}

form.signup__form {
    z-index: 1;
    opacity: 0;
}

.title {
    font-size: 2.2rem;
    color: #444;
    margin-bottom: 10px;
}

.input__field {
    max-width: 380px;
    width: 100%;
    height: 55px;
    background: #f0f0f0;
    margin: 10px 0;
    border-radius: 55px;
    display: grid;
    grid-template-columns: 15% 85%;
    padding: 0 .4rem;
}

.input__field > i {
    text-align: center;
    line-height: 55px;
    color: #acacac;
    font-size: 1.1rem;
}

.input__field > input {
    background: none;
    outline: none;
    border: none;
    line-height: 1;
    font-weight: 600;
    font-size: 1.1rem;
    color: #333;
}

.input__field > input::placeholder {
    color: #aaa;
    font-weight: 500;
}

.btn {
    width: 150px;
    height: 49px;
    border: none;
    outline: none;
    border-radius: 49px;
    cursor: pointer;
    background: #5995fd;
    color: #fff;
    text-transform: uppercase;
    margin: 10px 0;
    transition: .5s;
}

.btn:hover {
    background: #4d84e2;
}

.social__text {
    padding: 0.7rem 0;
    font-size: 1rem;
}

.social__media {
    display: flex;
    justify-content: center;
}

.social__icon {
    height: 46px;
    width: 46px;
    border: 1px solid #333;
    margin: 0 0 .45rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #333;
    font-size: 1.1rem;
    border-radius: 50%;
    transition: .3s;
}

.social__icon:hover {
    color: #4481eb;
    border-color: #4481eb;
}

.panels__container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: grid;
    grid-template-columns: repeat(2 , 1fr);
}

.panel {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    text-align: center;
    z-index: 7;
}

.left__panel {
    padding: 3rem 17% 2rem 12%;
    pointer-events: all;
}

.right__panel {
    padding: 3rem 12% 2rem 17%;
    pointer-events: none;
}

.panel > .content {
    color: #fff;
    transition: .9s .6s ease-in-out;
}

.panel h3 {
    font-weight: 600;
    line-height: 1;
    font-size: 1.5rem;
}

.panel p {
    font-size: .95rem;
    padding: 0.7rem 0;
}

.btn.transparent {
    margin: 0;
    background: none;
    border: 2px solid #fff;
    width: 130px;
    height: 41px;
    font-weight: 600;
    font-size: .8rem;
}

.right__panel > .content,
.right__panel > .image {
    transform: translateX(800px);
}

.image {
    width: 100%;
    transition: 1.1s .4s ease-in-out;
}

.container.signup__mode::before {
    transform: translate(100% , -50%);
    right: 52%;
}

.container.signup__mode .left__panel .image,
.container.signup__mode .left__panel .content{
    transform: translateX(-800px);
}

.container.signup__mode .right__panel .content,
.container.signup__mode .right__panel .image {
    transform: translateX(0px);   
}

.container.signup__mode .left__panel {
    pointer-events: none;
}

.container.signup__mode .right__panel {
    pointer-events: all;
}

.container.signup__mode .signin__signup {
    left: 25%;
}

.container.signup__mode form.signin__form {
    z-index: 1;
    opacity: 0;
}

.container.signup__mode form.signup__form {
    z-index: 2;
    opacity: 1;
}